<template>
  <div
    class="settings-templs__add"
    :class="{ 'settings-templs__add--open': isOpen }"
  >
    <div
      class="templs-add__btn"
      :class="{ 'templs-add__btn--open': isOpen }"
      @click="clearForm"
    >
      <font-awesome-icon :icon="['fas', 'plus']" size="xs" />
    </div>
    <div class="templs-add__form">
      <the-input-text :type="'text'" :label="'Название'" v-model.trim="name" />
      <the-input-text
        :type="'text'"
        :label="'Настоятель'"
        v-model.trim="abbot"
      />
      <the-input-text :type="'text'" :label="'Район'" v-model.trim="label" />
      <the-input-text
        :type="'number'"
        :label="'Количество отпеваний'"
        v-model.trim="minFun"
      />
      <the-button
        v-if="editID"
        :text="'Изменить'"
        @click="editTempl()"
      ></the-button>
      <the-button v-else :text="'Добавить'" @click="addTempl()"></the-button>
    </div>
  </div>
</template>

<script>
import TheButton from "../ui/TheButton.vue";
import TheInputText from "../ui/TheInputText.vue";

export default {
  name: "SettingsTemplsAdd",
  props: ["editID"],
  components: { TheInputText, TheButton },
  data() {
    return {
      isOpen: false,
      name: "",
      abbot: "",
      label: "",
      minFun: undefined,
    };
  },
  methods: {
    clearForm() {
      this.isOpen = !this.isOpen;
      this.name = "";
      this.abbot = "";
      this.label = "";
      this.minFun = undefined;
      this.$emit("clearForm", null);
    },
    async addTempl() {
      this.$store
        .dispatch("ADD_TEMPLS", {
          name: this.name,
          abbot: this.abbot,
          label: this.label,
          minFun: this.minFun,
        })
        .then(this.clearForm());
    },
    async editTempl() {
      this.$store
        .dispatch("EDIT_TEMPLS", [
          {
            name: this.name,
            abbot: this.abbot,
            label: this.label,
            minFun: this.minFun,
          },
          this.$props.editID,
        ])
        .then(this.clearForm());
    },
  },
  watch: {
    editID(newVal) {
      if (newVal) {
        let templs = this.$store.getters.GET_TEMPLS.find(
          (el) => el.id == newVal
        );
        this.isOpen = true;
        this.name = templs.name;
        this.abbot = templs.abbot;
        this.label = templs.label;
        this.minFun = templs.minFun;
      }
    },
  },
};
</script>

<style lang="scss" >
.settings-templs__add {
  width: calc(100vw - 10px);
  height: 50px;
  border: 2px solid $col-basic;
  margin-bottom: 5px;
  padding: 0 5px;
  transition: 0.3s ease-in-out;
  overflow: hidden;
  position: relative;
  &.settings-templs__add--open {
    height: 420px;
  }
}
.templs-add__btn {
  width: 100%;
  height: 50px;
  text-align: center;
  font-size: 1.9em;
  transition: 0.3s ease-in-out;
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  z-index: 3;
  &.templs-add__btn--open {
    width: 40px;
    height: 40px;
    right: 0;
    font-size: 1.6em;
    transform: rotateZ(45deg);
    color: $col-basic;
  }
}
</style>